import React, { useState, useRef } from 'react';
import Measure from 'react-measure'
import { SizeMe } from 'react-sizeme'

import Box from 'components/Box';
import Circle from 'components/Circle';
import Image from 'components/Image';
import isIE from 'components/utils/isIE'

import Body from './Body'
import bodySvg from './body.svg'
import bodyPng from './body.png'

const BodyPoints = ({ onNewDot, onDotClick, dots, isDownload, ...props }) => {
  const [dims, setBounds] = useState({})
  const bodyRef = useRef()
  const handleImageClick = e => {
    const position = bodyRef.current.getBoundingClientRect()
    onNewDot([(e.clientX - position.left) / position.width * 100, (e.clientY - position.top) / position.height * 100])
  }
  const handleDotClick = (i) => () => onDotClick(i);
  return (
    <Box position="relative" {...props}>
      <Measure bounds onResize={({ bounds }) => setBounds(bounds)}>
        {({ measureRef }) => (
          <Box pt="100%" position="relative" ref={measureRef}>
            <Box
              position="absolute"
              top="0"
              left="0"
              right="0"
              bottom="0"
              ref={bodyRef}
              onClick={onNewDot && handleImageClick}
            >
              {isDownload ? (isIE ? <Image src={bodyPng} /> : <Body width={dims.width} />) : <Image src={bodySvg} />}
            </Box>
          </Box>
        )}
      </Measure>

      {Boolean(dots.length) && dots.map((dot, i) => (
        <Box
          key={i}
          top={`${dot.position[1]}%`}
          left={`${dot.position[0]}%`}
          position="absolute"
          transform="translate(-50%, -50%)"
          onClick={onDotClick && handleDotClick(i)}
          width="10%"
        >
          <SizeMe>
            {({ size: { width } }) => (
              <Circle
                width={1}
                bg="orange"
                fontSize={i > 8 ? width * 0.5 : width * 0.7}
                left={i > 8 && '48%'}
                color="white"
                fontWeight="bold"
              >
                {i + 1}
              </Circle>
            )}
          </SizeMe>
        </Box>
      ))}
    </Box>
  );
};

export default BodyPoints;
