import React from 'react';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';
import styled, { css } from 'styled-components';

import themeGet from '@styled-system/theme-get';
import Box from './Box'

import withResponsive from '../hoc/withResponsive'

const Wrapper = styled(Box)`
  position: relative;
  &::after {
    ${({ isMobile }) => isMobile && 'content: "";'}
    display: block;
    position: absolute;
    border-style: solid;
    border-width: 5px 5px 0;
    border-color: ${themeGet('colors.primary')} transparent transparent;
    top: 1.4em;
    right: 0.5em;
  }
`

const selector = css`
  border: none;
  border-bottom: 1px solid ${themeGet('colors.lightOrange')};
  border-radius: 0;
  background-color: white;
  color: black;
  font-family: inherit;
  padding: 1em 52px 0.5em 0em;
  font-size: 1em;
`

const StyledNativeSelect = styled.select`
  width: 100%;
  display: block;
  -webkit-appearance: none;
  ${selector}
  > option {
    padding: 0;
    min-height: 1em;
  }
`

const StyledDropdown = styled(Dropdown)`
  .Dropdown-control {
    ${selector}
  }
  .Dropdown-menu {
    border: none;
    transform: translateY(1px);
  }
  .Dropdown-option {
    color: inherit;
    text-align: center;
    padding: 0.5em 1em;
    background-color: ${themeGet('colors.bodyBgOrange')};
    &.is-selected {
      background-color: ${themeGet('colors.primary')};
      color: ${themeGet('colors.white')};
    }
    &:hover {
      background-color: ${themeGet('colors.primaryHover')};
      color: ${themeGet('colors.white')};
    }
  }
  .Dropdown-arrow {
    border-color: ${themeGet('colors.primary')} transparent transparent;
    top: 1.4em;
    right: 0.5em;
  }
`

const CustomDropdown = ({
  isMobile,
  options,
  onChange,
  onBlur,
  value,
  name,
  placeholder,
  disabled,
  ...props
}) => {
  return (
    <Wrapper isMobile={isMobile} {...props}>
      {isMobile ? (
        <StyledNativeSelect
          name={name}
          onChange={onChange}
          onBlur={onBlur}
          value={value}
          disabled={disabled}
        >
          <option value="">{placeholder}</option>
          {options.map(({ label }, i) => (
            <option key={i} value={i}>{label}</option>
          ))}
        </StyledNativeSelect>
      ) : (
        <StyledDropdown
          onChange={e => onChange({ target: { name, value: e.value }, persist: () => {} })}
          options={options.map(({ label }, value) => ({ value, label }))}
          value={value && Object.assign({}, options[+value], { value })}
          placeholder={placeholder}
          disabled={disabled}
        />
      )}
    </Wrapper>
  );
};

CustomDropdown.defaultProps = {
  placeholder: '請選擇',
}

export default withResponsive(CustomDropdown);
