import React, { PureComponent } from 'react';
import { Formik, Form, ErrorMessage } from 'formik';
import get from 'lodash/get';

import FullpageVerticalCenter from 'components/FullpageVerticalCenter';
import Box from 'components/Box';
import Flex from 'components/Flex';
import Text from 'components/Text';
import Modal from 'components/Modal';
import Input from 'components/Input';
import Button from 'components/Button';
import Dropdown from 'components/Dropdown';
import Title from 'components/Title';
import Progress from 'components/Progress';
import Container from 'components/Container';
import { mobileOrDesktop } from 'components/ThemeProvider/theme'

import BodyPoints from '../BodyPoints';
import StepNumber from './StepNumber';
import Layout from '../../Layout';
import withResponsive from '../../../hoc/withResponsive'
import littlebody from './littlebody.svg'

export const options = [{label: '1-2天'}, {label: '3-7天'}, {label: '7天以上'}]

// const schema = object().shape({
//   pain: string().required('請輸入症狀'),
//   duration: string().required('請選擇持續天數')
// })

class Step1 extends PureComponent {
  state = {

  }

  handleNewDot = (pos) => {
    const { newDot, dots } = this.props
    newDot(pos)
    this.setState({
      openDot: dots.length,
    })
  }

  handleDotClick = index => {
    this.setState({ openDot: index })
  }

  handleClose = () => {
    this.setState({ openDot: false })
  }

  handleRemove = () => {
    const { removeDot } = this.props
    removeDot(this.state.openDot)
    this.setState({ openDot: false })
  }

  render() {
    const { dots, setDotData, isMobile, onNext } = this.props
    const { openDot } = this.state
    // console.log(dots[openDot], openDot)
    return (
      <Layout bg="lightBgOrange" overflowX="hidden" headerBg={mobileOrDesktop('lightBgOrange', 'auto')}>
        <StepNumber>1</StepNumber>
        <Container py={mobileOrDesktop('1em', '2em')} position="relative">
          <Progress step={1} />
          <Box mt="1em">
            <Title mobileWidth={3 / 10} title="點擊人偶圖來標示不適處" description="看診前先詳細紀錄不舒服的狀況，有助於在診間中與醫生討論病情" icon={littlebody} />
            <Box
              my={mobileOrDesktop('1em', '0.5em')}
              mx={mobileOrDesktop('10%', '30%')}
              bg="bodyBgOrange"
              px={mobileOrDesktop('2.25em', '2.75em')}
              pt={mobileOrDesktop('1em', '2em')}
              borderRadius="1em"
              boxShadow="0px 3px 4px 4px rgba(0,0,0,0.1)"
            >
              <BodyPoints
                onNewDot={this.handleNewDot}
                onDotClick={this.handleDotClick}
                dots={dots}
              />
              <Text py="1em" textAlign="center">再次點擊不適部位即可更改</Text>
            </Box>
            <Modal
              isOpen={typeof openDot === 'number'}
              customStyles={{ width: isMobile && '85%' }}
            >
              <Formik
                initialValues={{
                  duration: get(dots[openDot], 'duration'),
                  pain: get(dots[openDot], 'pain'),
                }}
                onSubmit={(values) => {
                  setDotData({
                    index: openDot,
                    ...values
                  })
                  this.handleClose()
                }}
              >
                {({ handleChange, values }) => (
                  <Form>
                    <Container px={mobileOrDesktop('0', 'auto')}>
                      <Text fontSize="1.25em" pt="1em" color="titleBlue">症狀描述</Text>
                      <Text>依自身狀況填入不適程度與頻率</Text>
                      <Input
                        mt="1em"
                        placeholder="請填入欲補充細節"
                        name="pain"
                        onChange={handleChange}
                        value={values.pain}
                      />
                      <ErrorMessage name="pain" />
                      <Text mt="1em" fontSize="1.25em" color="titleBlue">持續天數</Text>
                      <Dropdown options={options} name="duration" onChange={handleChange} value={values.duration} />
                      <ErrorMessage name="duration" />
                      <Flex justifyContent="center" mt="2em">
                        <Button mx={mobileOrDesktop('0.25em', '0.5em')} px={mobileOrDesktop('1.75em', '2em')} onClick={this.handleRemove}>{get(dots[openDot], 'pain') ? '刪除':'取消'}</Button>
                        <Button mx={mobileOrDesktop('0.25em', '0.5em')} px={mobileOrDesktop('1.75em', '2em')} type="submit">確認</Button>
                      </Flex>
                    </Container>
                  </Form>
                )}
              </Formik>
            </Modal>
            <Box mt="2em" textAlign="center">
              <Button px="3em" onClick={onNext}>下一步</Button>
            </Box>
          </Box>
        </Container>
      </Layout>
    );
  }
}

export default withResponsive(Step1);
